import { promises } from "fs";
// Sleep promise, used temporarily until syncTo is working to delay updated queries.
function sleep(time) {
	return new Promise((resolve) => setTimeout(resolve, time));
}

export class NewDatabaseModal extends Component {
	state = {
		id:
			this.props._db.environment === "hosted"
				? this.props._db.account + "/"
				: "",
		error: null,
		showSnapshotInput: false,
		showNewDatabaseFromScratchInput: false,
		snapshotButtonStatus: false,
		newDatabaseButtonStatus: false,
		selectedDatabase: "",
		selectedDbSnapshotList: "",
		selectedSnapshot: "",
		selectedMethod: "filepath",
	};

	setSelectedDbSnapshotList(db) {
		const { ip, token } = this.props._db;
		const opts = {
			endpoint: "list-snapshots",
			ip: ip,
			network: db.split("/")[0],
			db: db.split("/")[1],
			auth: token,
			noRedirect: true,
		};
		this.getDatabaseSnapshotList(opts).then((res) => {
			this.setState((prevState) => ({
				...prevState,
				selectedDbSnapshotList: res,
			}));
		});
	}
	refreshForm = () => {
		this.setState({
			id:
				this.props._db.environment === "hosted"
					? this.props._db.account + "/"
					: "",
			error: null,
			loading: false,
		});
	};

	getValidationState = () => {
		if (this.props._db.environment === "hosted") {
			const { id } = this.state;
			const length = id.length;
			if (!id.startsWith(this.props._db.account + "/")) return "warning";
			else if (!/^[a-zA-Z0-9-]*\/[a-zA-Z0-9]+$/.test(id)) return "warning";
			// make sure at least account_name + '/' + one character
			else if (length < this.props._db.account.length + 2) return "success";
			else return "success";
		} else {
			let regex = /^[a-z][a-z0-9-]*\/[a-z][a-z0-9-]*$/;
			if (this.state.id.length > 0 && this.state.id.match(regex) !== null) {
				return "success";
			}
			return "warning";
		}
	};

	handleChange(key, e) {
		let newState = {};
		newState[key] = e.target.value;
		this.setState(newState);
	}

	handleChangeSelectDB(e) {
		e.preventDefault();

		this.setState({
			selectedDatabase: e.target.value ? e.target.value : null,
		});
		this.setSelectedDbSnapshotList(e.target.value);
	}

	handleChangeSelectMethod(e) {
		this.setState({
			selectedMethod: e.target.value ? e.target.value : null,
		});
	}
	handleChangeSelectSnapshot(e) {
		this.setState({
			selectedSnapshot: e.target.value,
			snapshot: e.target.value,
		});
	}

	handleCreateFromScratchOnClick() {
		this.setState((prevState) => ({
			...prevState,
			showNewDatabaseFromScratchInput: !this.state
				.showNewDatabaseFromScratchInput,
			showSnapshotInput: false,
			newDatabaseButtonStatus: !this.state.newDatabaseButtonStatus,
			snapshotButtonStatus: false,
		}));
	}

	handleCreateFromSnapshotOnclick() {
		this.setState((prevState) => ({
			...prevState,
			showSnapshotInput: !this.state.showSnapshotInput,
			showNewDatabaseFromScratchInput: false,
			snapshotButtonStatus: !this.state.snapshotButtonStatus,
			newDatabaseButtonStatus: false,
		}));
	}

	onGenerate = (e) => {
		const downloaded = process.env.REACT_APP_ENVIRONMENT === "downloaded";

		this.setState({ loading: true });
		e.preventDefault();

		let blockNumber =
			this.state.blockNumber && Number(this.state.blockNumber) > 0
				? Number(this.state.blockNumber)
				: null;

		const tx = downloaded
			? {
					"db/id": this.state.id,
					snapshot: this.state.snapshotPath,
					snapshotBlock: blockNumber,
			  }
			: ["new-db", { "db/id": this.state.id }];
		const endpoint = downloaded ? "new-db" : "action";
		const { ip, token } = this.props._db;
		const opts = {
			ip: ip,
			endpoint: endpoint,
			body: tx,
			auth: token,
		};

		const refreshDbs = this.props._db.refreshDbs;

		const request = () => {
			flureeFetch(opts)
				.then((response) => {
					if (response.status === 200) {
						refreshDbs(ip);
						this.props.newDatabaseModalToggle();
					} else {
						this.setState({ error: response.message, loading: false });
					}
				})
				.catch((response) => {
					// error
					this.setState({
						error:
							response.json.message ||
							response.error ||
							JSON.stringify(response),
						loading: false,
					});
				});
		};
		request();
	};
	getDatabaseSnapshotList(dbOpts) {
		return new Promise((resolve, reject) => {
			flureeFetch(dbOpts).then((response) => {
				if (response.json) {
					let res = response.json;

					resolve(res);
				} else {
					let result = {
						msg: response.message ? [response.message] : [response],
					};
					resolve(result);
				}
			});
		});
	}

	render() {
		const {
			newDatabaseModalToggle,
			_db,
			databasesSnapshotList,
			openApiServer,
			openApiServerStatus,
		} = this.props;

		const arrayOfDatabases = _db.dbs;

		return (
			<Draggable enableUserSelectHack={false}>
				<Modal show onHide={() => newDatabaseModalToggle()}>
					<Modal.Header closeButton>
						<Modal.Title style={{ fontSize: "20px", fontWeight: "lighter" }}>
							<i className="fas fa-cube" /> &nbsp; Create New Ledger
							<hr style={{ marginBottom: "0px" }} />
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<FormGroup
							style={{
								display: "flex",
								justifyContent: "space-around",
								width: "100%",
							}}
						>
							<Button
								style={{ borderRadius: "32px" }}
								bsStyle="info"
								disabled={this.state.newDatabaseButtonStatus}
								onClick={() => this.handleCreateFromScratchOnClick()}
							>
								Blank Ledger
							</Button>
							{"   "}
							{/* this.props.openApiServerStatus */}
							{_db.environment === "downloaded" && openApiServerStatus && (
								<Button
									style={{ borderRadius: "32px" }}
									disabled={this.state.snapshotButtonStatus}
									bsStyle="info"
									onClick={() => this.handleCreateFromSnapshotOnclick()}
								>
									Ledger from Snapshot
								</Button>
							)}
						</FormGroup>
						{/* The create database from scratch section */}
						{this.state.showNewDatabaseFromScratchInput ? (
							<Fragment>
								<h3>
									Note: A database id uniquely identifies a database within a
									network. A database begins with a network name followed by a
									forward slash "/".
									<br></br>
								</h3>
								<p>
									<em>Example: "test/one1"</em>
								</p>
								<Form onSubmit={this.onGenerate.bind(this)}>
									<ControlLabel style={{ margin: "20px 0" }}>
										Database Id
									</ControlLabel>
									<FormGroup
										controlId="formBasicText"
										validationState={this.getValidationState()}
									>
										<span>
											<FormControl
												onMouseDown={(e) => {
													e.stopPropagation();
												}}
												type="text"
												value={this.state.id}
												placeholder="Begin database id"
												onChange={(e) => this.handleChange("id", e)}
											/>
										</span>

										<HelpBlock>
											{" "}
											{this.props._db.environment === "hosted" &&
												`Database name must be prefixed by your account name: ${this.props._db.account}`}{" "}
											A ledger name can only include lowercase letters, numbers,
											and hyphen " - ".
										</HelpBlock>
									</FormGroup>
									<FormGroup className="text-right">
										<Button
											style={{ borderRadius: "32px" }}
											type="submit"
											bsStyle="info"
											disabled={
												this.getValidationState() === "warning" ||
												this.state.loading
											}
										>
											Create
										</Button>
									</FormGroup>
									{this.state.error ? (
										<Alert
											bsStyle="danger"
											onDismiss={() => this.setState({ error: null })}
										>
											<h4>Error</h4>
											<p>{this.state.error}</p>
										</Alert>
									) : null}
								</Form>
							</Fragment>
						) : null}
						{/* The create database from snapshot section */}
						{this.state.showSnapshotInput ? (
							<Fragment>
								<h3>
									Note: A ledger id uniquely identifies a database within a
									network. A database begins with a network name followed by a
									/.
								</h3>
								<Form onSubmit={this.onGenerate.bind(this)}>
									<ControlLabel style={{ margin: "20px 0" }}>
										Ledger Id
									</ControlLabel>
									<FormGroup
										controlId="formBasicText"
										validationState={this.getValidationState()}
									>
										<FormControl
											onMouseDown={(e) => {
												e.stopPropagation();
											}}
											type="text"
											value={this.state.id}
											placeholder="Begin database id"
											onChange={(e) => this.handleChange("id", e)}
										/>
										<HelpBlock>
											{" "}
											{this.props._db.environment === "hosted" &&
												`Database name must be prefixed by your account name: ${this.props._db.account}`}{" "}
											A ledger name can only include lowercase letters, numbers,
											and -.
										</HelpBlock>
									</FormGroup>
									<FormGroup>
										<ControlLabel style={{ margin: "20px 0" }}>
											Select Method:
										</ControlLabel>
										<select
											value={this.state.selectedMethod}
											onChange={(e) => this.handleChangeSelectMethod(e)}
											style={{ marginLeft: "10px" }}
										>
											{/* <option value="">---- : ----</option> */}
											<option value="filepath">Enter File Path</option>
											<option value="snapshotlist">
												Choose From Snapshot list
											</option>
										</select>
									</FormGroup>
									{this.state.selectedMethod === "filepath" && (
										<Fragment>
											<ControlLabel style={{ margin: "20px 0" }}>
												File Path :
											</ControlLabel>
											<FormGroup controlId="formBasicText">
												<FormControl
													onMouseDown={(e) => {
														e.stopPropagation();
													}}
													type="text"
													placeholder="Enter File Path"
													onChange={(e) => this.handleChange("snapshotPath", e)}
												/>
												<HelpBlock>
													<p>
														Please enter file's relative path, for example:
														mynetwork/mydb/snapshot/1576183568139.avro"
													</p>
												</HelpBlock>
											</FormGroup>
										</Fragment>
									)}
									{this.state.selectedMethod === "snapshotlist" && (
										<Fragment>
											<FormGroup>
												<ControlLabel style={{ margin: "20px 0" }}>
													Select Ledger:
												</ControlLabel>
												<select
													value={this.state.selectedDatabase}
													onChange={(e) => this.handleChangeSelectDB(e)}
													style={{ marginLeft: "10px" }}
												>
													{/* <option>--- : ---</option> */}
													{arrayOfDatabases.map((database, index) => {
														return <option value={database}>{database}</option>;
													})}
												</select>
												<br></br>
												<ControlLabel style={{ margin: "20px 0" }}>
													Select Snapshot:
												</ControlLabel>
												<select
													value={this.state.selectedSnapshot}
													onChange={(e) => this.handleChangeSelectSnapshot(e)}
													style={{ marginLeft: "10px" }}
												>
													{/* <option>--- : ---</option> */}
													{
														this.state.selectedDbSnapshotList &&
														this.state.selectedDbSnapshotList.length != 0 ? (
															this.state.selectedDbSnapshotList.map(
																(snapshot) => {
																	return (
																		<option value={snapshot}>{snapshot}</option>
																	);
																}
															)
														) : (
															<option value={null}>Nothing here</option>
														)
														// <option value={null}>Select A Ledger</option>
													}
												</select>
											</FormGroup>
										</Fragment>
									)}

									<ControlLabel style={{ margin: "20px 0" }}>
										Block Number (Optional):
									</ControlLabel>
									<FormGroup controlId="formBasicText">
										<FormControl
											type="number"
											placeholder="Enter Block Number"
											onChange={(e) => this.handleChange("blockNumber", e)}
										/>
									</FormGroup>
									<FormGroup className="text-right">
										<Button
											style={{ borderRadius: "32px" }}
											type="submit"
											bsStyle="info"
											disabled={
												this.getValidationState() === "warning" ||
												this.state.loading
											}
										>
											Create
										</Button>
									</FormGroup>
									{this.state.error ? (
										<Alert
											bsStyle="danger"
											onDismiss={() => this.setState({ error: null })}
										>
											<h4>Error</h4>
											<p>{this.state.error}</p>
										</Alert>
									) : null}
								</Form>
							</Fragment>
						) : null}
					</Modal.Body>
				</Modal>
			</Draggable>
		);
	}
}

class NewUserModal extends Component {
	state = {
		username: "",
		error: null,
	};

	refreshForm = () => {
		this.setState({
			username: "",
			error: null,
			loading: false,
		});
	};

	getValidationState() {
		const { username } = this.state;
		const emailRegex = /^[\w-+]+(\.[\w]+)*@[\w-]+(\.[\w]+)*(\.[a-z]{2,})$/;
		if (username.length < 1) return "success";
		else if (!emailRegex.test(username)) return "error";
		else return "success";
	}

	handleChange(e) {
		this.setState({ username: e.target.value });
	}

	onGenerate = (e) => {
		e.preventDefault();
		this.setState({ loading: true });
		const { account, token } = this.props._db;
		const { newUserModalToggle, getAccountInfo } = this.props;

		const action = [
			"new-user",
			{
				username: this.state.username.toLowerCase(),
				account: account,
				sendEmail: true,
			},
		];

		const opts = {
			endpoint: "action",
			auth: token,
			body: action,
		};

		flureeFetch(opts)
			.then((response) => {
				if (response.status === 200) {
					this.setState({ username: "", loading: false });
					getAccountInfo();
					newUserModalToggle();
				} else {
					this.setState({ error: response.message, loading: false });
				}
			})
			.catch((response) => {
				// error
				this.setState({
					error: response.message || response.error || JSON.stringify(response),
					loading: false,
					dbName: this.props.account + ".",
				});
			});
	};

	render() {
		const { newUserModalToggle } = this.props;

		return (
			<Draggable enableUserSelectHack={false}>
				<Modal show onHide={() => newUserModalToggle()}>
					<Modal.Header closeButton>
						<Modal.Title style={{ fontSize: "20px", fontWeight: "lighter" }}>
							<i className="fas fa-user" /> &nbsp; Add User to Account
							<hr />
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form onSubmit={this.onGenerate.bind(this)}>
							<ControlLabel className="mt20 mb20">
								User Email Address
							</ControlLabel>
							<FormGroup
								controlId="formBasicText"
								validationState={this.getValidationState()}
							>
								<FormControl
									onMouseDown={(e) => {
										e.stopPropagation();
									}}
									type="text"
									value={this.state.username}
									placeholder="Enter email address for new user"
									onChange={this.handleChange.bind(this)}
									autoFocus
								/>
								<FormControl.Feedback />
							</FormGroup>
							<p>User will receive an email explaining next steps.</p>
							<FormGroup className="text-right">
								<Button
									bsStyle="primary"
									type="submit"
									disabled={
										this.getValidationState() === "error" || this.state.loading
									}
								>
									Create
								</Button>
							</FormGroup>
							{this.state.error ? (
								<Alert bsStyle="danger" onDismiss={() => this.refreshForm()}>
									<h4>Error</h4>
									<p>{this.state.error}</p>
								</Alert>
							) : null}
						</Form>
					</Modal.Body>
				</Modal>
			</Draggable>
		);
	}
}

export class ConfirmDatabaseDeletionModal extends Component {
	state = {
		confirmDeletion: "",
		confirmDeletionAuthIDAndPrivateKey: null,
		error: null,
		confirmDeletionAuthID: null,
		confirmDeletionPrivateKey: null,
	};

	deleteDB = (e, db, ip) => {
		const { openApiServer, displayError } = this.props._db;
		e.preventDefault();
		const downloaded = process.env.REACT_APP_ENVIRONMENT === "downloaded";
		let opts;
		const tx = {
			"db/id": db,
		};

		const endpoint = downloaded ? "delete-db" : "action";
		const refreshDbs = this.props._db.refreshDbs;
		const request = (options) => {
			flureeFetch(options)
				.then((response) => {
					if (response.status === 200) {
						refreshDbs(ip);
					} else {
						this.setState({ error: response.message, loading: false });
						displayError(response.message);
					}
				})
				.catch((response) => {
					let result = response.json || response;
					let errorMessage =
						result.message || result.error || JSON.stringify(response);
					this.setState({
						error: errorMessage,
						loading: false,
					});
					displayError(errorMessage);
				});
		};
		if (openApiServer) {
			opts = {
				ip: ip,
				endpoint: endpoint,
				body: tx,
				auth: this.props.token,
			};
		} else {
			let authId = this.state.confirmDeletionAuthID;
			let privateKey = this.state.confirmDeletionPrivateKey;

			const baseUri = gateway(ip) || "";
			const { headers, body } = signRequest(
				"POST",
				`${baseUri}/fdb/delete-db`,
				JSON.stringify({ "db/id": db, auth: authId }),
				privateKey,
				authId
			);

			opts = {
				ip: ip,
				endpoint: endpoint,
				body: JSON.parse(body),
				headers: headers,
				auth: this.props.token,
				noRedirect: true,
			};

			//Lois changes
			// I think that this should work with just fdb/delete-db
			// Check what is in IP too, you may be able to concatenate that
			// using something like getHost(ip) which is in SignCommand.js
			//let privateKey = this.confirmDeletionPrivateKey;

			// let closedApiEndpoint = "http://localhost:8090/fdb/delete-db"; //need this because it is the original
			// let body = JSON.stringify({
			// 	...tx,
			// 	auth: authId,
			// });
			// opts = signRequest("POST", closedApiEndpoint, body, privateKey, authId);
			// console.log(opts);
		}

		const { confirmDatabaseDeletionModalToggle } = this.props;
		request(opts);
		confirmDatabaseDeletionModalToggle();
	};

	handleChangeForConfirmDeleteTextbox = (e) => {
		this.setState({
			confirmDeletion: e.target.value,
		});
	};
	handleChangeForConfirmPrivateKeyTextbox = (e) => {
		this.setState({
			confirmDeletionPrivateKey: e.target.value,
		});
	};
	handleChangeForConfirmAuthIDTextbox = (e) => {
		this.setState({
			confirmDeletionAuthID: e.target.value,
		});
	};
	getValidationState = () => {
		const { openApiServer } = this.props._db;
		if (!openApiServer) {
			if (
				this.state.confirmDeletionAuthID &&
				this.state.confirmDeletionPrivateKey
			) {
				return false;
			} else {
				return true;
			}
		} else {
			if (this.state.confirmDeletion === "confirm") {
				return false;
			} else {
				return true;
			}
		}
	};
	render() {
		const { confirmDatabaseDeletionModalToggle, db, ip } = this.props;
		const { openApiServer } = this.props._db;
		console.log(this.props._db);
		return (
			<Fragment>
				<Draggable enableUserSelectHack={false}>
					{/* <Button variant="primary" onClick={handleShow}>
				Launch demo modal
			</Button> */}

					<Modal show onHide={() => confirmDatabaseDeletionModalToggle()}>
						<Modal.Header closeButton>
							<Modal.Title>
								Are you sure you want to DELETE this database?{" "}
							</Modal.Title>
						</Modal.Header>
						<Form onSubmit={(e) => this.deleteDB(e, db, ip)}>
							{openApiServer && (
								<Modal.Body>
									<p>Please, enter "confirm" to DELETE database.</p>
									<FormGroup>
										<FormControl
											onMouseDown={(e) => {
												e.stopPropagation();
											}}
											type="text"
											value={this.state.confirmDeletion} //add this to state
											placeholder="Enter 'confirm' to DELETE database"
											onChange={(e) =>
												this.handleChangeForConfirmDeleteTextbox(e)
											}
											autoFocus
										/>
									</FormGroup>
								</Modal.Body>
							)}
							{!openApiServer && (
								<Modal.Body>
									<p>
										Server Open-Api status is currently set to <i>'false'</i>.
										Please provide an <b>AuthID</b> and <b>Private Key</b> to{" "}
										<span style={{ color: "red" }}>DELETE</span> database.
									</p>

									<FormGroup>
										<p>AuthID</p>
										<FormControl
											onMouseDown={(e) => {
												e.stopPropagation();
											}}
											type="text"
											value={this.state.confirmDeletionAuthID}
											placeholder="Enter 'AuthID' to DELETE database"
											onChange={(e) =>
												this.handleChangeForConfirmAuthIDTextbox(e)
											}
											autoFocus
										/>
										<br></br>
										<p>Private Key</p>
										<FormControl
											onMouseDown={(e) => {
												e.stopPropagation();
											}}
											type="text"
											value={this.state.confirmDeletionPrivateKey} //add this to state
											placeholder="Enter 'Private Key' to DELETE database"
											onChange={(e) =>
												this.handleChangeForConfirmPrivateKeyTextbox(e)
											}
											autoFocus
										/>
									</FormGroup>
								</Modal.Body>
							)}

							<Modal.Footer>
								<Button
									style={{ borderRadius: "32px" }}
									bsStyle="danger"
									type="submit"
									disabled={this.getValidationState()}
								>
									DELETE
								</Button>
							</Modal.Footer>
							{this.state.error ? (
								<Alert
									style={{ margin: "0 10px 10px" }}
									bsStyle="danger"
									onDismiss={() => this.setState({ error: null })}
								>
									<h4>Error</h4>
									<p>{this.state.error}</p>
								</Alert>
							) : null}
						</Form>
					</Modal>
				</Draggable>
			</Fragment>
		);
	}
}

export class ConfirmSnapshotCreationModal extends Component {
	state = { confirmSnapshotCreation: "" };
	createSnapshot(db, ip) {
		//debugger;
		const downloaded = process.env.REACT_APP_ENVIRONMENT === "downloaded";
		// this.setState({ loading: true });

		const tx = downloaded
			? {
					"db/id": db,
			  }
			: ["new-db", { "db/id": this.state.id }];
		const endpoint = downloaded ? "snapshot" : "action";

		const opts = {
			ip: ip,
			endpoint: endpoint,
			body: tx,
			auth: this.props.token,
		};

		const refreshDbs = this.props._db.refreshDbs;

		flureeFetch(opts)
			.then((response) => {
				debugger;
				//console.log("create snapshot function response");
				if (response.status === 200) {
					//console.log("success");
					//set corresponding database snapshot to disabled
					debugger;
					console.log(response);
					refreshDbs(ip);
					this.setState({ snapshotCreated: response.json });
					this.showSnapshotInfoModalToggle();
					//enable button for this database
				} else {
					this.setState({ error: response.message, loading: false });
				}
			})
			.catch((response) => {
				// error
				this.setState({
					error:
						response.json.message || response.error || JSON.stringify(response),
					loading: false,
				});
			});
	}
	handleChange = (e) => {
		this.setState({
			confirmSnapshotCreation: e.target.value,
		});
	};
	getValidationState = () => {
		if (this.state.confirmSnapshotCreation === "confirm") {
			return false;
		} else {
			return true;
		}
	};
	render() {
		const { showConfirmSnapshotCreationModalToggle, db, ip } = this.props;
		//console.log(`new database format ${db}`);
		return (
			<Fragment>
				<Draggable enableUserSelectHack={false}>
					{/* <Button variant="primary" onClick={handleShow}>
				Launch demo modal
			</Button> */}

					<Modal show onHide={() => showConfirmSnapshotCreationModalToggle()}>
						<Modal.Header closeButton>
							<Modal.Title>
								<h3>
									This Snapshot might not be available for use immediately.{" "}
								</h3>
							</Modal.Title>
						</Modal.Header>
						<Form onSubmit={() => this.createSnapshot(db, ip)}>
							<Modal.Body>
								<p style={{ marginBottom: "10px" }}>
									Please, enter "confirm" to CREATE Snapshot.
								</p>
								<FormGroup>
									<FormControl
										onMouseDown={(e) => {
											e.stopPropagation();
										}}
										type="text"
										value={this.state.confirmSnapshotCreation} //add this to state
										placeholder="Enter 'confirm' to CREATE Snapshot"
										onChange={(e) => this.handleChange(e)}
										autoFocus
									/>
								</FormGroup>
							</Modal.Body>
							<Modal.Footer>
								<Button
									style={{ borderRadius: "32px" }}
									bsStyle="danger"
									type="submit"
									disabled={this.getValidationState()}
								>
									CREATE
								</Button>
							</Modal.Footer>
							{this.state.error ? (
								<Alert
									style={{ margin: "0 10px 10px" }}
									bsStyle="danger"
									onDismiss={() => this.setState({ error: null })}
								>
									<h4>Error</h4>
									<p>{this.state.error}</p>
								</Alert>
							) : null}
						</Form>
					</Modal>
				</Draggable>
			</Fragment>
		);
	}
}
