class AccountInfo extends Component {
	state = {
		newDatabaseModal: false, // toggle to show modal for adding a new database
		dataBasesWithInfo: [],
		isLoadingDatabaseInfo: true,
		configModal: false,
		confirmDatabaseDeletionModal: false,
		checkDbs: this.props._db,
		dbsLength: this.props._db.dbs.length,
	};
	componentDidMount() {
		if (this.props._db.environment === "hosted") {
			this.getAccountInfo();
		}

		this.loadDatabaseInfo();

		const { _db } = this.props;

		let nwStateIp = _db.ip;
		let nwStateParams = {
			endpoint: "nw-state",
			ip: nwStateIp,
			noRedirect: true,
		};

		flureeFetch(nwStateParams)
			.then((response) => {
				let statusCheck = this.getOpenApiServerStatus();
				if (response.json["open-api"]) {
					// Only load snapshot list when open-api server status is true
					// this.loadDatabasesSnapshotList(
					// 	"list-snapshots",
					// 	this.getDatabasesSnapshotList
					// );
				}
				if (statusCheck === undefined) {
					this.setOpenApiServerStatus(response.json["open-api"]);
					this.setState({
						openApiServer: response.json["open-api"],
					});
				} else {
					let openApiServerStatusShow = this.getOpenApiServerStatus();
					this.setState({
						openApiServer: response.json["open-api"],
						showServerOpenApiAlert: openApiServerStatusShow,
					});
				}
			})
			.catch((error) => {
				this.displayError(error);
			});
	}
	componentDidUpdate(prevProps, prevState) {
		//used to ensure re-render on deletion or creation of db
		if (prevState.dbsLength !== this.state.dbsLength) {
			this.loadDatabaseInfo();
		}
	}
	getAccountInfo() {
		const accountId = this.props._db.account;
		const token = this.props._db.token;
		let userArray = [];
		let maxDbs;
		let maxUsers;

		const opts = {
			endpoint: "accounts",
			auth: token,
			body: {},
			noRedirect: true,
		};

		flureeFetch(opts)
			.then((res) => {
				let accounts = res.json || res;

				accounts.forEach((account) => {
					if (get(account, "account/id") === accountId) {
						let users = get(account, "account/users");
						users.forEach((user) =>
							userArray.push(get(user, "_user/username"))
						);
						maxDbs = get(account, "account/maxDbs");
						maxUsers = get(account, "account/maxUsers");
					}
				});
				return {
					maxUsers: maxUsers,
					maxDbs: maxDbs,
					users: userArray,
					db: null,
					getTokenModal: false,
					newDatabaseModal: false,
					newUserModal: false,
					deleteDatabaseModal: false,
					archiveDatabaseModal: false,
					deleteArchiveModal: false,
					removeUserModal: false,
				};
			})
			.then((newState) => {
				this.setState(newState);
			})
			.catch((error) => {
				//console.log(`This is an ${error}`)

				if (error.status === 401) {
					// main token expired, need to log back in.
					window.location = "/signout";
				} else {
					//console.log(`This is an ${error}`)
					this.props._db.displayError(error);
				}
			});
	}

	displayError(error) {
		this.setState({ error: error });
	}
	loadDatabaseInfo() {
		let databaseInfoRequests = [];
		const { ip, token, dbs, db } = this.props._db;

		let fullDatabases = dbs.map((db) => [...db.split("/"), db]);
		fullDatabases.forEach((fullDb) => {
			//Get ledger stats from new 'ledger-stats' endpoint;

			const optsForLedgerStats = {
				endpoint: "ledger-stats",
				ip: ip,
				network: fullDb[0],
				db: fullDb[1],
				auth: token,
			};
			databaseInfoRequests.push(
				this.getDatabasesInfo(optsForLedgerStats, fullDb[2])
			);
		});

		Promise.all(databaseInfoRequests)
			.then((res) => {
				this.setState({
					dataBasesWithInfo: [...res],
					isLoadingDatabaseInfo: false,
				});
			})
			.catch((error) => {
				if (error.status === 401) {
					this.props._db.displayConfig();
				} else {
					this.setState({
						error: JSON.stringify(error),
						isLoadingDatabaseInfo: false,
						loading: false,
					});
				}
			});
	}

	getDatabasesInfo(optsForLedgerStats, databaseName) {
		//const {ip, token, dbs, openApi} = this.props._db

		const { openApi } = this.props._db;

		return new Promise((resolve, reject) => {
			flureeFetch(optsForLedgerStats)
				.then((response) => {
					if (response.json && response.json.data) {
						let res = response.json.data;
						resolve({ ...res, databaseName });
					} else {
						// 'error' returned in response
						let result = {
							msg: response.message ? response.message : response,
						};
						resolve({ ...result, databaseName });
					}
				})
				.catch((error) => {
					reject(error);
				});
		});
	}

	setOpenApiServerStatus = (openApiServerStatus) => {
		const config = {
			openApiServerStatusShow: openApiServerStatus ? false : true,
		};
		localStorage.setItem("openApiServerConfig", JSON.stringify(config));
	};

	getOpenApiServerStatus = () => {
		let config = localStorage.getItem("openApiServerConfig");
		config = JSON.parse(config);

		let openApiServerStatusShow = get(config, "openApiServerStatusShow");

		return openApiServerStatusShow;
	};
	static getDerivedStateFromProps(nextProps, prevState) {
		//used to ensure re-render on deletion or creation of db
		if (nextProps._db !== prevState.checkDbs) {
			return { dbsLength: nextProps._db.dbs.length };
		} else return null;
	}

	sortBy(array, attrName) {
		array.sort((a, b) => {
			let nameA = get(a, attrName, "z").toUpperCase();
			let nameB = get(b, attrName, "z").toUpperCase();
			return nameA < nameB ? -1 : nameA > nameB ? 1 : 0;
		});

		return array;
	}

	archiveDatabaseModalToggle(dbName) {
		this.setState({
			archiveDatabaseModal: !this.state.archiveDatabaseModal,
			dbName: dbName,
		});
	}

	backupDatabaseModalToggle(dbName) {
		this.setState({
			backupDatabaseModal: !this.state.backupDatabaseModal,
			dbName: dbName,
		});
	}

	newDatabaseModalToggle = () => {
		this.setState({ newDatabaseModal: !this.state.newDatabaseModal });
		this.forceUpdate();
	};

	newUserModalToggle = () => {
		this.setState({ newUserModal: !this.state.newUserModal });
	};

	removeUserModalToggle(username) {
		this.setState({
			removeUserModal: !this.state.removeUserModal,
			username: username,
		});
	}

	render() {
		const _db = this.props._db;
		const {
			account,
			dbs,
			inactiveDbs,
			environment,
			ip,
			token,
		} = this.props._db;

		const downloaded = environment === "downloaded";
		const hostedVersion = environment === "hosted";

		const { maxDbs, maxUsers, users } = this.state;
		const accountLength = account.length;
		const dataBasesText = dbs.length === 1 ? "LEDGER" : "LEDGERS";
		const numOfDatabases = dbs.length;
		if ((downloaded && !dbs) || (!downloaded && !maxDbs)) {
			return <div className="loading1"> Loading... </div>;
		}

		return (
			<div
				style={{
					paddingLeft: "40px",
					marginTop: "40px",
					height: "100%",
					width: "100%",
				}}
			>
				{this.state.newDatabaseModal === true ? (
					<NewDatabaseModal
						_db={_db}
						databasesSnapshotList={this.state.databasesSnapshotList}
						newDatabaseModalToggle={this.newDatabaseModalToggle}
						openApiServerStatus={this.state.openApiServer}
					/>
				) : null}
				{this.state.newUserModal === true ? (
					<NewUserModal
						{...this.props}
						_db={_db}
						newUserModalToggle={this.newUserModalToggle}
						getAccountInfo={this.getAccountInfo.bind(this)}
					/>
				) : null}

				{/* {this.state.archiveDatabaseModal === true ? <ArchiveDatabaseModal {...this.props} _db={_db} archiveDatabaseModalToggle={this.archiveDatabaseModalToggle.bind(this)}  getAccountInfo={this.getAccountInfo.bind(this)} /> : null} */}
				{/* {this.state.backupDatabaseModal === true ? <BackupDatabaseModal {...this.props} _db={_db} account={account} dbName={this.state.dbName} backupDatabaseModalToggle={this.backupDatabaseModalToggle.bind(this)}  getAccountInfo={this.getAccountInfo.bind(this)} /> : null} */}
				{this.state.removeUserModal === true ? (
					<RemoveUserModal
						{...this.props}
						_db={_db}
						account={account}
						username={this.state.username}
						history={this.props.history}
						removeUserModalToggle={this.removeUserModalToggle.bind(this)}
						getAccountInfo={this.getAccountInfo.bind(this)}
					/>
				) : null}
				<div className="row">
					<div className="col-sm-9" style={{ marginBottom: "30px" }}>
						<h2 style={{ color: "#737171" }}>
							<span className="account-header text-uppercase">
								&nbsp;&nbsp;
								{downloaded ? "FlureeDB" : `Account: ${this.props._db.account}`}
							</span>
						</h2>
					</div>

					<div className="col-sm-3 start-tour" style={{ marginBottom: "30px" }}>
						<Button
							style={{ borderRadius: "7px" }}
							bsStyle="info"
							onClick={() => hopscotch.startTour(tour, 0)}
						>
							Fluree Tips
						</Button>
					</div>
				</div>
				{hostedVersion && (
					<div>
						<div
							className="row"
							id="panel-stats"
							style={{ marginBottom: "50px" }}
						>
							<div className="col-sm-6 col-md-3">
								<PanelStat
									iconClass="fas fa-cube bg-info"
									stat={maxDbs - dbs.length}
									statText={
										maxDbs - dbs.length === 1
											? "Database Available"
											: "Databases Available"
									}
								/>
							</div>
							<div className="col-sm-6 col-md-3">
								<PanelStat
									iconClass="fas fa-cube bg-success"
									stat={dbs.length}
									statText={
										dbs.length === 1
											? "Database in Account"
											: "Databases in Account"
									}
								/>
							</div>
							<div className="col-sm-6 col-md-3">
								<PanelStat
									iconClass="fas fa-user bg-info"
									stat={maxUsers - users.length}
									statText={
										maxUsers - users.length === 1
											? "User Available"
											: "Users Available"
									}
								/>
							</div>
							<div className="col-sm-6 col-md-3">
								<PanelStat
									iconClass="fas fa-user bg-success"
									stat={users.length}
									statText={
										users.length === 1 ? "User in Account" : "Users in Account"
									}
								/>
							</div>
						</div>
						<div className="row col-md-12">
							<div className="col-md-4 col-sm-4" style={{ marginRight: "10" }}>
								<h3 className="pb10" style={{ color: "#737171" }}>
									ACCOUNT USERS ({maxUsers - users.length} available)
								</h3>
								<div className="list-group">
									{users.map((user) => (
										<Users
											key={user}
											username={user}
											removeUserModalToggle={this.removeUserModalToggle.bind(
												this
											)}
										/>
									))}
								</div>
								<p />
								<div className="text-right">
									<Button
										bsStyle="primary"
										id="new-user-button"
										onClick={this.newUserModalToggle}
									>
										{" "}
										<i className="fas fa-user-plus" /> &nbsp;Add User{" "}
									</Button>
								</div>
							</div>
							<div className=" col-sm-8 col-md-8">
								<div>
									<h3
										className="pb10"
										style={{ color: "#737171", textAlign: "center" }}
									>
										{`${dataBasesText} (${numOfDatabases})`}
									</h3>
									<div>
										{this.state.isLoadingDatabaseInfo
											? "Loading...."
											: this.state.dataBasesWithInfo.map((dataBaseInfo) => (
													<Database
														key={dataBaseInfo.databaseName}
														db={dataBaseInfo.databaseName}
														block={dataBaseInfo.block}
														size={dataBaseInfo.size}
														flakes={dataBaseInfo.flakes}
														dbLength={dbs.length}
														ip={ip}
														token={token}
														_db={_db}
														msg={dataBaseInfo.msg}
														newDatabaseModalToggle={this.newDatabaseModalToggle}
														openApiServerStatus={this.state.openApiServer}
													/>
											  ))}
										<div className="text-center mt20">
											<Button
												style={{ borderRadius: "32px", textAlign: "center" }}
												bsStyle="info"
												id="new-database-button"
												onClick={this.newDatabaseModalToggle} //note1
											>
												<i className="fas fa-plus" /> &nbsp;Add Ledger{" "}
											</Button>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				)}

				{downloaded && (
					<div className="row">
						<div>
							<h3
								className="pb10"
								style={{ color: "#737171", textAlign: "center" }}
							>
								{`${dataBasesText} (${numOfDatabases})`}
							</h3>
							<div style={{textAlign: "center"}}>
								
									{" "}
									<Button
										style={{ borderRadius: "32px" }}
										bsStyle="info"
										id="new-database-button"
										onClick={this.newDatabaseModalToggle} //note1
									>
										<i className="fas fa-plus" /> &nbsp;Add Ledger{" "}
									</Button>
								
							</div>
							<div>
								{this.state.isLoadingDatabaseInfo
									? "Loading...."
									: this.state.dataBasesWithInfo.map((dataBaseInfo) => (
											<Database
												key={dataBaseInfo.databaseName}
												db={dataBaseInfo.databaseName}
												block={dataBaseInfo.block}
												size={dataBaseInfo.size}
												flakes={dataBaseInfo.flakes}
												dbLength={dbs.length}
												ip={ip}
												token={token}
												_db={_db}
												msg={dataBaseInfo.msg}
												newDatabaseModalToggle={this.newDatabaseModalToggle}
												openApiServerStatus={this.state.openApiServer}
											/>
									  ))}
							</div>

							{/* <div className="row">
								<div className="col-sm-12 text-center mt20">
								
								</div>
							</div> */}
						</div>
					</div>
				)}
				{/* <div className="row">
					<div className="col-sm-10">
						<Footer />
					</div>
				</div> */}
			</div>
		);
	}
}

export default AccountInfo;



